    object-fit: cover;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  #sbs-2329 .cs-video:hover {
    cursor: pointer;
  }
  #sbs-2329 .cs-video:hover .cs-play {
    transform: translate(-50%, -50%) scale(1.2);
  }
  #sbs-2329 .cs-play {
    width: 6.25rem;
    height: 6.25rem;
    display: flex;
    pointer-events: none;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.8);
    border: none;
    position: absolute;
    z-index: 10;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: transform 0.3s, opacity 0.3s;
  }
  #sbs-2329 .cs-play.cs-hide {
    transform: translate(-50%, -50%) scale(0) !important;
    opacity: 0;
  }
  #sbs-2329 img.cs-play-icon {
    width: 1.5rem;
    height: auto;
  }
  #sbs-2329 .cs-ul {
    width: 100%;
    max-width: 39.375rem;
    margin: 0 0 2rem 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
  #sbs-2329 .cs-li {
    list-style: none;
    margin: 0 0 1.5rem;
    padding: 0 0 1.5rem;
    border-bottom: 1px solid #e7e7e8;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 1.25rem;
    /* remove the border and spacing on the last cs-li */
  }
  #sbs-2329 .cs-li:last-of-type {
    margin: 0;
    padding: 0;
    border: none;
  }
  #sbs-2329 .cs-icon {
    /* 40px - 60px */
    width: clamp(2.5rem, 5vw, 3.75rem);
    height: auto;
    display: block;
  }
  #sbs-2329 .cs-h3 {
    font-size: 1.25rem;
    font-weight: 700;
    line-height: 1.2em;
    text-align: left;
    margin: 0 0 0.5rem 0;
    color: var(--headerColor);
  }
  #sbs-2329 .cs-li-text {
    font-size: 1rem;
    line-height: 1.5em;
    text-align: left;
    margin: 0;
    color: var(--bodyTextColor);
  }
  #sbs-2329 .cs-picture {
    width: 100%;
    height: 22.5rem;
    display: block;
  }
  #sbs-2329 .cs-picture img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  #sbs-2329 .cs-button-solid {
    font-size: 1rem;
    font-weight: 700;
    /* 46px - 56px */
    line-height: clamp(2.875rem, 5.5vw, 3.5rem);
    text-align: center;
    text-decoration: none;
    min-width: 9.375rem;
    margin: 0;
    padding: 0 1.5rem;
    background-color: var(--primary);
    color: var(--bodyTextColorWhite);
    display: inline-block;
    position: relative;
    z-index: 1;
  }
  #sbs-2329 .cs-button-solid:before {
    content: "";
    width: 0%;
    height: 100%;
    background: #000;
    opacity: 1;
    border-radius: 0.25rem;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    transition: width 0.3s;
  }
  #sbs-2329 .cs-button-solid:hover:before {
    width: 100%;
  }
}
/* Tablet - 768px */
@media only screen and (min-width: 48rem) {
  #sbs-2329 .cs-container {
    max-width: 80rem;
    gap: 1rem;
    justify-content: space-between;
  }
  #sbs-2329 .cs-content-group {
    display: grid;
    grid-template-columns: auto auto;
    /* 32px - 48px */
    column-gap: clamp(2rem, 3vw, 3rem);
  }
  #sbs-2329 .cs-content {
    grid-column: span 2;
  }
  #sbs-2329 .cs-flex-wrapper {
    flex-direction: row;
    align-items: stretch;
  }
  #sbs-2329 .cs-content-picture {
    width: 28vw;
    max-width: 22.625rem;
    height: 100%;
    flex: none;
    grid-column: span 1;
  }
  #sbs-2329 .cs-ul-wrapper {
    padding-bottom: 4.5rem;
  }
  #sbs-2329 .cs-ul {
    margin-top: 0;
  }
}
/* Desktop - 1024px */
@media only screen and (min-width: 64rem) {
  #sbs-2329 .cs-container {
    flex-direction: row;
    align-items: stretch;
  }
  #sbs-2329 .cs-ul-wrapper {
    width: 40vw;
    max-width: 24.0625rem;
    flex: none;
  }
  #sbs-2329 .cs-picture {
    width: 50vw;
    max-width: 25.8125rem;
    height: auto;
  }
}
/* Dark Mode */
@media only screen and (min-width: 0rem) {
  body.dark-mode #sbs-2329 .cs-title,
  body.dark-mode #sbs-2329 .cs-h3,
  body.dark-mode #sbs-2329 .cs-li-text {
    color: var(--bodyTextColorWhite);
  }
  body.dark-mode #sbs-2329 .cs-text,
  body.dark-mode #sbs-2329 .cs-li-text {
    opacity: 0.8;
  }
  body.dark-mode #sbs-2329 .cs-li {
    border-color: rgba(255, 255, 255, 0.2);
  }
  body.dark-mode #sbs-2329 .cs-icon {
    filter: grayscale(1) brightness(1000%);
  }
}
.cs-logo svg {
  color: #1a1a1a; /* black */
}

body.dark-mode .cs-logo svg {
  color: #ffffff; /* white */
}